body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.App {
  text-align: center;
  padding: 20px;
}

.task-form {
  margin-bottom: 20px;
}

.task-form input, .task-form select, .task-form button {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 5px;
  padding: 10px;
}

.error-message {
  color: red;
  margin: 10px 0;
}

.board {
  justify-content: space-around;
  display: flex;
}

.column {
  background-color: #fff;
  border-radius: 4px;
  width: 30%;
  padding: 10px;
  box-shadow: 0 0 10px #0000001a;
}

.card {
  background-color: #ececec;
  border-radius: 4px;
  margin: 10px 0;
  padding: 10px;
}

.card button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-top: 10px;
  padding: 5px;
}

.card button:hover {
  background-color: #0056b3;
}
